.cards-view-container {
  padding: 1rem;
  background: white;
  min-height: 400px;
  font-family: 'Poppins', sans-serif;
  overflow: visible;
}

/* Loading and Error States */
.loading-container,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  text-align: center;
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #e2e8f0;
  border-top: 3px solid #BFAB86;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 0.75rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-container .error-icon {
  color: #ef4444;
  margin-bottom: 0.75rem;
}

.error-container h3 {
  color: #374151;
  margin-bottom: 0.5rem;
}

.error-container p {
  color: #6b7280;
  margin-bottom: 1rem;
}

/* Cards Header */
.cards-header [dir='rtl']{
  text-align: right;
}
.cards-header {
  margin-bottom: 1.5rem;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-bottom: 1px solid #e5e7eb;
}

.cards-header h2 {
  color: #5F646D;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.cards-header p {
  color: #979797;
  font-size: 1rem;
}

/* Search and Filters */
.filters-section {
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.search-bar {
  margin-bottom: 1rem;
}

.search-input-wrapper {
  position: relative;
  max-width: 400px;
}

.search-icon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  color: #9ca3af;
  pointer-events: none;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 0.875rem;
  transition: all 0.2s ease;
  background: white;
}

.search-input:focus {
  outline: none;
  border-color: #BFAB86;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

.filters-row {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: end;
}

.filter-group {
  display: flex;
  flex-direction: column;
  min-width: 120px;
}

.filter-group label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.25rem;
}

.filter-select {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 0.875rem;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
}

.filter-select:focus {
  outline: none;
  border-color: #BFAB86;
  box-shadow: 0 0 0 3px rgba(212, 170, 58, 0.1);
}

/* Pagination */
.pagination-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
  padding: 1rem;
  background: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.pagination-info {
  color: #6b7280;
  font-size: 0.875rem;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.pagination-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border: 1px solid #d1d5db;
  background: white;
  color: #374151;
  border-radius: 6px;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.pagination-btn:hover:not(:disabled) {
  background: #f3f4f6;
  border-color: #9ca3af;
}

.pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.page-numbers {
  display: flex;
  gap: 0.25rem;
}

.page-btn {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #d1d5db;
  background: white;
  color: #374151;
  border-radius: 6px;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.page-btn:hover:not(:disabled):not(.active) {
  background: #f3f4f6;
  border-color: #9ca3af;
}

.page-btn.active {
  background: #BFAB86;
  border-color: #BFAB86;
  color: white;
}

.page-btn.dots {
  border: none;
  background: none;
  cursor: default;
}

.page-btn:disabled {
  cursor: not-allowed;
}

/* Cards Grid */
.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1rem;
  padding: 0.5rem 0;
  overflow: visible;
}

.department-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
  position: relative;
  min-height: 160px;
  display: flex;
  flex-direction: column;
  overflow: visible;
}

.department-card:hover {
  border-color: #BFAB86;
  box-shadow: 0 2px 8px rgba(21, 134, 56, 0.15);
}

/* Card Header */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.75rem;
}

.card-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: linear-gradient(135deg, #BFAB86 0%, #fdd90e 100%);
  border-radius: 8px;
  color: white;
  flex-shrink: 0;
}

.options-menu {
  margin-left: auto;
}

/* Card Content */
.card-content {
  flex: 1;
  margin-bottom: 0.75rem;
}

.card-title {
  color: #5F646D;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 0.25rem 0;
  line-height: 1.3;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.card-id {
  color: #6b7280;
  font-size: 0.8rem;
  font-weight: 500;
  margin-bottom: 0.75rem;
}

.card-meta {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #6b7280;
  font-size: 0.8rem;
}

.meta-icon {
  color: #9ca3af;
  flex-shrink: 0;
}

.department-type {
  font-weight: 500;
  color: #BFAB86;
  text-transform: capitalize;
}

/* Card Footer */
.card-footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.75rem;
  border-top: 1px solid #e5e7eb;
}

.card-actions {
  display: flex;
  gap: 0.25rem;
}

.action-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
  background: transparent;
}

.edit-btn {
  border-color: #29ABE2;
  color: #29ABE2;
}

.edit-btn:hover {
  background-color: #BBDEFB;
  color: #29ABE2;
}

.delete-btn {
  color: #dc2626;
}

.delete-btn:hover {
  background-color: #fef2f2;
  color: #b91c1c;
}

.status-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.status-badge.active {
  background-color: #dcfce7;
  color: #186432;
}

.status-badge.inactive {
  background-color: #fee2e2;
  color: #991b1b;
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 1.5rem;
  text-align: center;
}

.empty-icon {
  color: #9ca3af;
  margin-bottom: 0.75rem;
}

.empty-state h3 {
  color: #374151;
  margin-bottom: 0.5rem;
}

.empty-state p {
  color: #6b7280;
}

/* Responsive Design */
@media (max-width: 768px) {
  .cards-view-container {
    padding: 0.75rem;
  }
  
  .filters-section {
    padding: 0.75rem;
  }
  
  .filters-row {
    flex-direction: column;
    gap: 0.75rem;
    align-items: stretch;
  }
  
  .filter-group {
    min-width: auto;
  }
  
  .pagination-section {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
    text-align: center;
  }
  
  .pagination-controls {
    justify-content: center;
  }
  
  .page-numbers {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .cards-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 0.75rem;
  }
  
  .department-card {
    padding: 0.75rem;
    min-height: 140px;
  }
  
  .card-icon {
    width: 32px;
    height: 32px;
  }
  
  .card-title {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .cards-grid {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  
  .department-card {
    padding: 0.75rem;
    min-height: 130px;
  }
  
  .card-header {
    flex-direction: column;
    gap: 0.75rem;
    align-items: flex-start;
  }
  
  .card-actions {
    align-self: flex-end;
  }
}

/* RTL Support */
.cards-view-container.rtl .card-header {
  flex-direction: row-reverse;
}

.cards-view-container.rtl .card-footer {
  flex-direction: row-reverse;
}

.cards-view-container.rtl .meta-item {
  flex-direction: row-reverse;
}

/* Button Styles */
.btn {
  font-family: 'Poppins', sans-serif;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

/* Ensure English text uses Poppins */
.btn.ltr,
html[dir="ltr"] .btn {
  font-family: 'Poppins', sans-serif;
}

/* Ensure Arabic text uses IBM Plex Sans Arabic */
.btn.rtl,
html[dir="rtl"] .btn {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}

.btn-primary {
  background: linear-gradient(135deg, #BFAB86 0%, #B68A35 100%);
  color: white;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #B68A35 0%, #92722A 100%);
  transform: translateY(-1px);
  box-shadow: 0 2px 6px rgba(212, 170, 58, 0.3);
}

/* Animation */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.department-card {
  animation: fadeInUp 0.3s ease-out;
}

.department-card:nth-child(1) { animation-delay: 0.1s; }
.department-card:nth-child(2) { animation-delay: 0.2s; }
.department-card:nth-child(3) { animation-delay: 0.3s; }
.department-card:nth-child(4) { animation-delay: 0.4s; }
.department-card:nth-child(5) { animation-delay: 0.5s; }
.department-card:nth-child(6) { animation-delay: 0.6s; }

/* Delete Modal Styles */
.delete-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.delete-modal {
  background: white;
  border-radius: 16px;
  padding: 2rem;
  max-width: 480px;
  width: 90%;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.delete-modal-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background: #D4AF5F;
  color: #5F646D;
  border-radius: 8px 8px 0 0;
}

.delete-modal-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  color: white;
  flex-shrink: 0;
}

.delete-modal-header h3 {
  margin: 0;
  color: #1B1D21;
  font-size: 1.5rem;
  font-weight: 700;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  flex: 1;
  order: 1;
}

.close-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  color: #1B1D21;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.close-btn:hover {
  background-color: rgba(27, 29, 33, 0.1);
  color: #1B1D21;
}

.delete-modal-body {
  margin-bottom: 2rem;
}

.delete-modal-message {
  margin: 0 0 1rem 0;
  color: #4b5563;
  font-size: 1.125rem;
  line-height: 1.6;
  justify-content: center;
}

.delete-modal-department {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.delete-modal-department strong {
  color: #1f2937;
  font-weight: 600;
  font-size: 1rem;
}

.department-code {
  color: #6b7280;
  font-size: 0.875rem;
  font-weight: 500;
}

.delete-modal-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 600;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
}

.btn-secondary {
  background-color: #f3f4f6;
  color: #374151;
  border: 1px solid #d1d5db;
}

.btn-secondary:hover {
  background-color: #e5e7eb;
  border-color: #9ca3af;
}

.btn-danger {
  background-color: #dc3545;
  color: white;
}

.btn-danger:hover {
  background-color: #c82333;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(220, 53, 69, 0.3);
}

/* RTL Support for delete modal */
.rtl .delete-modal-actions {
  justify-content: flex-start;
}

.rtl .delete-modal-header {
  flex-direction: row-reverse;
}

.rtl .delete-modal-header h3 {
  order: 2;
}

.rtl .delete-modal-header .close-btn {
  order: 1;
}

.rtl .delete-modal-department {
  flex-direction: row-reverse;
}
